<template>
    <div style='position:relative;height:90vh'>
        <div class="inneroverlay" v-if="open_overlay">
            <div class="card" style='height:100%'>
                <div class="card-header">
                    <div class="flex">
                        <strong>Details for {{details[0].item_code}}</strong>
                        <i class="fa fa-window-close" aria-hidden="true" style='margin-left:auto;margin-top:0.25rem;margin-right:0.5rem;cursor:pointer' @click="close_overlay"></i>
                    </div>
                </div>            
                <div class="card-body card-block">
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Item Code
                        </div>:
                        <div class="col-8">
                            {{ details[0].item_code }}
                        </div>
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Name
                        </div>:
                        <div class="col-8">
                            {{ details[0].item_eng_name }}
                        </div>
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                           Location
                        </div>:
                        <div class="col-8">
                            {{ details[0].sub_location_code }}
                        </div>
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                           Details
                        </div>:
                        <div v-if="details[0].sn!=undefined" class="col-8">
                            {{ details[0].sn }}
                        </div>
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                           Remarks
                        </div>:
                        <div class="col-8">
                            {{ details[0].remarks }}
                        </div>
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                           Status
                        </div>:
                        <div v-if="status=='pending'" class="col-8">
                            {{ details[0].stocktake }}
                        </div>
                        <div v-if="status=='write_off'" class="col-8">
                             <b  style='color:rgb(165,28,37)'>Write-off Completed</b>
                        </div>
                    </div>
                </div>
                <div class="card-footer" v-if="status=='pending'">                
                    <button type="button" class="btn btn-primary btn-sm" @click="write_off(details[0].item_code)">
                        <i class="fa fa-paper-plane"></i> Confirm Write Off 
                    </button>
                    <button type="button" class="btn btn-danger btn-sm" @click="rej_write_off(details[0].item_code)">
                        <i class="fa fa-ban"></i> Reject
                    </button>
                </div>
            </div>
        </div> 
        <div v-if="ready">
            <div class="col-12 px0">
                <div class="col-12 px0 flex">
                    <div class="col-2" style='margin-bottom:0.75rem;padding-left:0'>
                        <select class="form-control select" v-model="status" @change="fetch_list">
                            <option value='pending'>Pending</option>
                            <option value='write_off'>Write Off Completed</option>
                        </select>
                    </div>
                    <div v-if="status=='write_off'" class="col-2" style='margin-bottom:0.75rem'>
                        <button type="button" class="form-control" @click="export_file">Export Write Off Items</button>
                    </div>
                    <div class="col-2" style="margin-left:auto;padding-right:0">
                        <button class="form-control" type='button' @click="redirect">Add Write Off Item(s)</button>
                    </div>
                </div>
                <v-data-table
                        :headers="headers"
                        :items="item_list"
                        class="elevation-1"
                        :rows-per-page-items="[15]"
                        disable-initial-sort
                    >
                    <template v-slot:items="props">
                        <tr class="click_row" style='cursor:pointer' @click="overlay(props.item.item_code)">
                            <td><u>Details</u></td>
                            <td>{{ props.item.item_code }}</td>
                            <td>{{ props.item.item_name }}</td>
                            <td>{{ props.item.details }}</td>
                            <td>{{ props.item.applicant }}</td>
                            <td>{{ props.item.reason }}</td>
                        </tr>
                    <template v-slot:pageText="props">
                        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                    </template>
                    
                    </template>
                    </v-data-table>
            </div>            
        </div>        
    </div>
</template>
<script>
export default {
    data(){
        return{
            headers: [
                { text: 'Select',value :'id',sortable: false},
                { text: 'Item Code',value:'item_code',sortable: true},
                { text: 'Item Name',value:'name',sortable: true},              
                { text: 'Details',value:'details',sortable: true},
                { text: 'Applicant',value:'action_by',sortable: true},
                { text: 'Reason',value:'reason',sortable: true},
            ],
            item_list:[],
            details:[],
            status:'pending',
            open_overlay:false,
            ready:false,
        }
    },
    async created(){
        await this.fetch_list();
        this.ready = true;
    },
    async mounted(){

    },
    methods:{
        rej_write_off(code){
            var formData = new FormData();
            formData.append('code',code);
            this.$confirm("Confirm rejecting write-off item :"+code+" ?").then(
                ()=>{
                    axios.post("inventory/web/backend/rej_write_off.php",formData)
                        .then(
                            (response)=>{
                                if(response.data=="OK"){
                                    this.$alert("Successfully Rejected Write-off for : "+code).then(
                                        ()=>{
                                            this.open_overlay = false;
                                            this.fetch_list();
                                        }
                                    )
                                }
                            }
                        )
                }
            )         
        },
        redirect(){
            this.$router.push('inventory_list');
        },
        async write_off(item_code){
            await this.$confirm('Confirm Write Off item : '+item_code+' ?').then(
                async()=>{
                    var formData = new FormData();
                    formData.append('item_code',item_code);
                    await axios.post('inventory/web/backend/confirm_write_off.php',formData)
                        .then(
                            async(response)=>{
                                var res = response.data;
                                if(res=='OK'){                                   
                                    this.$alert('Successfully approved write off for item '+item_code); 
                                    await this.fetch_list();    
                                    this.open_overlay=false;                                 
                                }
                            }
                        )
                }
            )                             
        },
        async export_file(){
            await axios.get('inventory/web/backend/export_write_off.php')
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            const url = window.URL.createObjectURL(new Blob([response.data]));
                            const link = document.createElement('a');
                            link.href = url;
                            link.setAttribute('download', 'write_off.csv'); //or any other extension
                            document.body.appendChild(link);
                            link.click();
                        } else {
                            this.$alert('No records');
                        }
                    }
                )
        },
        close_overlay(){
            this.open_overlay = false;
        },
        async fetch_list(){
            var formData = new FormData();
            formData.append('status',this.status);
            await axios.post('inventory/web/backend/fetch_write_off_item.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.item_list = response.data;
                        } else {
                            this.item_list = [];
                        }
                    }
                )
        },
        async overlay(item_code){
            var formData = new FormData();
            formData.append('item_code',item_code);
            await axios.post('inventory/web/backend/item_details.php',formData)
                .then(
                    (response)=>{   
                        if(response.data!='empty'){
                            this.details = response.data;
                            this.open_overlay = true;
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
    .inner_row{
        display:flex;
        flex-direction:row;
        margin-bottom:1rem;
    }
    .flex{
        display:flex;
        flex-direction: row;
    }
    .inneroverlay{
        background-color: rgba(0,0,0,0.4);
        position: absolute; 
        left: 0; 
        top:0;
        bottom:0;
        right: 0; 
        margin:auto;
        width: 50%;
        height:50%;
        z-index: 50;
    }
    .px0{
        padding-left:0;
        padding-right:0;
    }
    .wrapper{
        position:relative;
    }
    .click_row{
        cursor:pointer;
    }
    .fa-window-close{
        transition: all 0.15s ease-in-out;
        color:rgba(255, 255, 255, 0.55);
    }
    .fa-window-close:hover{
        color:rgba(255, 255, 255, 1);
    }
</style>


